import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, NgForm } from '@angular/forms';
import { CalendarOptions, DateFormatter, EventSourceFunc, EventSourceInput } from '@fullcalendar/angular';
import { UnitEmployeeMapping } from '../../../master/unit-master/masters/unitEmployeeMapping';

import { Router } from '@angular/router';
import { routerTransition } from '../../../router.animations';
import { Branch } from '../../../master/service-center/masters/branch';
import { Department } from '../../../master/department/masters/department';
import { Unit } from '../../../master/unit-master/masters/unit';
import { Employee } from '../../../master/unit-master/masters/Employee';
import { Appointment } from '../../masters/appointment';
import { Patient } from '../../../patient/master/patient';
import { LookupCategory } from '../../../master/lookup-module/masters/lookup-category';
import { LookupEntity } from '../../../master/lookup-module/masters/lookup-entity';
import { BranchServiceService } from '../../../master/service-center/services/branch-service.service';
import { DepartmentService } from '../../../master/department/services/department.service';
import { PatientService } from '../../../patient/service/patient.service';
import { UnitMasterService } from '../../../master/unit-master/services/unit-master.service';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { AppointmentService } from '../../services/appointment.service';
import { LookupService } from '../../../master/lookup-module/services/lookup.service';


@Component({
  selector: 'app-new-appointment',
  templateUrl: './new-appointment.component.html',
  styleUrls: ['./new-appointment.component.css'],
  animations: [routerTransition()],
})
export class NewAppointmentComponent implements OnInit {
	
	branchList: Array<Branch>;
	departmentList: Array<Department>;
	unitList: Array<Unit>;
	unitEmployeeMappingList: Array<UnitEmployeeMapping>;
	employee: Employee = new Employee();
	branchId: number;
	departmentId:number;
	unitId: number;
	employeeId: number;
	appointmentForm = new  FormGroup({});
	date: Date = new Date();
	dateStr: String;
	event: any;
	interval : number = 15;
	appointmentTimeSlotList: Array<number> = [];
	appointmentTimeSlot:Array<String> = [];
	appointmentList: Array<Appointment> = new Array<Appointment>();
	addAppointment : Appointment = new Appointment();
	patientList: Array<Patient> = new Array<Patient>();
	newPatientList : Array<[Patient,Date]> = new Array<[Patient,Date]>();
	patient: Patient = new Patient;
	startDate: Date = new Date();
	endDate: Date = new Date();
	lookupCategoryList: Array<LookupCategory> = [];
	appointmentStatusList: Array<LookupEntity>;
	appointmentStatusTypeList: Array<LookupEntity>;
	lookupEntity : LookupEntity = new LookupEntity();
	appointmentStatus: LookupEntity = new LookupEntity();
	appointmentStatusType : LookupEntity = new LookupEntity();
	toBeDeleteAppointment : Appointment = new Appointment();
	cancelledAppointmentStatus: LookupEntity  = new LookupEntity();
	@Input() patientDetails : Patient = new Patient;
  constructor(private branchService: BranchServiceService,private departmentService: DepartmentService, 
  	private unitService: UnitMasterService,private patientService: PatientService,private formBuilder: FormBuilder,
  	private appointmentService: AppointmentService,private config: NgbModalConfig, private modalService: NgbModal,
  	private lookupService: LookupService,private router: Router) { 
	config.backdrop = "static";
	config.keyboard = false;
}
  ngOnInit(){
	this.endDate = new Date(this.endDate.setHours(24,0,0,0));
	this.appointmentForm = this.formBuilder.group({
		branch: new FormControl(null),
		department: new FormControl(null),
		unit: new FormControl(null),
		doctor: new FormControl(null),
		})
	this.branchService.getBranch().subscribe(data =>{
		this.branchList = data;
	});
	this.departmentService.getAllDepartments().subscribe(data =>{
		this.departmentList = data;
	});
	this.unitService.getUnit().subscribe(data =>{
		this.unitList = data;
	});
	this.patientService.getPatientList().subscribe(data =>{
		this.patientList = data;
	});
	this.appointmentService.getAppointmentList().subscribe(data =>{
		this.appointmentList = data;
		//console.warn(data);
	});
	this.lookupService.getLookupCategoryList().subscribe(data =>{
		this.lookupCategoryList = data;
		//console.warn(data);
	});
	this.lookupService.getLookupEntityList(19).subscribe(data =>{
		this.appointmentStatusList = data;
		//console.error(data);
	});
		this.lookupService.getLookupEntityList(20).subscribe(data =>{
		this.appointmentStatusTypeList = data;
		//console.warn(data);
	})
	
	
	}
calendarOptions: CalendarOptions = {
    initialView: 'dayGridMonth',
    dateClick: this.handleDateClick.bind(this), // bind is important!
    events: [
      { title: 'event 1', date: '2021-06-23' },
      { title: 'event 2', date: '2021-22-06' }
    ],
    
    slotMinWidth: 20,
    contentHeight: 400,
    
  };
  

  handleDateClick(arg) {
    alert('date click! ' + arg.date);
    this.date = arg.date;
    this.newPatientList = []
    //Logic For showing waiting patinet list
    for(let appointment of this.appointmentList)
		{
		if((appointment.doctor.employeeId == this.employee.employeeId))
		{
			
			let appointTime = new Date(this.date);
			let fromDate = new Date(appointment.fromDate);
			if(fromDate.getDate() == appointTime.getDate())
			{
				//console.error(this.employee.employeeId)
					if(appointment.appointmentStatus.lookupId == 44)
					{
						let fromDate = new Date(appointment.fromDate)
						this.newPatientList.push([appointment.patient,fromDate]);
						//console.log("Function called")
					}
				
			}
		}
	}
    this.dateStr = arg.dateStr;
    const startDate = arg.date;
    const endDate = arg.date.getTime() + 86400000;
    //console.log(endDate);
    const date = new Date(endDate);
    //console.log(date);
    //console.log(arg);
   // console.log(startDate, date.getHours());
    this.startDate = new Date(startDate);
    this.endDate = new Date(endDate);
    this.appointmentService.getAppointmentTimeList(startDate,date,this.interval).subscribe(data =>{
	this.appointmentTimeSlotList = data;
	this.appointmentTimeSlot = [];
	this.appointmentTimeSlotList.forEach(data =>{
		const date = new Date(data);
		var hour = (date.getHours()<10?'0':'')+date.getHours();
		let min = (date.getMinutes()<10?'0':'') + date.getMinutes();
		var time = hour + ":"+min;
		this.appointmentTimeSlot.push(time);
	})
	});
  }
  
  getDepartmentList(value)
  {
	this.branchId = value.id;
	//console.log(this.branchId);
  }
  getUnitList(value)
  {
	this.departmentId = value.departmentId;
	//console.log(this.departmentId);
  }
  getDoctorList(event){
	
	this.unitId = event.unitId;
	this.unitEmployeeMappingList = event.unitEmployeeMappingList;
	//console.log(event.unitEmployeeMappingList);
	//console.log(this.appointmentForm.value.doctor);
  }
  showDoctorName(event)
  {
	
	this.employee  = event;
	let interval = this.employee.slot.lookupValue;
	this.interval = Number(interval.substring(0,2));
	this.appointmentService.getAppointmentTimeList(this.startDate,this.endDate,this.interval).subscribe(data =>{
	this.appointmentTimeSlotList = data;
	this.appointmentTimeSlot = [];
	this.appointmentTimeSlotList.forEach(data =>{
		const date = new Date(data);
		var hour = (date.getHours()<10?'0':'')+date.getHours();
		let min = (date.getMinutes()<10?'0':'') + date.getMinutes();
		var time = hour + ":"+min;
		this.appointmentTimeSlot.push(time);
	})
	});
	//Logic For showing waiting patinet list
	this.newPatientList = [];
    for(let appointment of this.appointmentList)
		{
		if((appointment.doctor.employeeId == this.employee.employeeId))
		{
			
			let appointTime = new Date(this.date);
			let fromDate = new Date(appointment.fromDate);
			if(fromDate.getDate() == appointTime.getDate())
			{
				//console.error(this.employee.employeeId)
					if(appointment.appointmentStatus.lookupId == 44)
					{
						let fromDate = new Date(appointment.fromDate)
						this.newPatientList.push([appointment.patient,fromDate]);
						//console.log("Function called")
					}
				
			}
		}
	}
	//console.warn(this.interval);
	//console.log("This is ",this.employee)
  }
  returnDate(appointmentTime){
	//console.warn(this.date);
	this.date.setHours(Number(appointmentTime.toString().substr(0,2)));
	this.date.setMinutes(Number(appointmentTime.toString().substr(3,2)));
	//this.date.setMinutes(Number(appointmentTime.toString().substr(2,4)));
	//console.warn(this.date);
	return this.date;
}

scheduleEvent(eventSchedulerForm,appointmentTime){
	//this.addAppointment = this.appointmentForm.value;
	this.addAppointment.branch = this.appointmentForm.value.branch;
	this.addAppointment.department = this.appointmentForm.value.department;
	this.addAppointment.unit = this.appointmentForm.value.unit;
	this.addAppointment.doctor = this.employee;
	this.addAppointment.patient = this.patient;
	for(let appointmentStatus of this.appointmentStatusList){
		if(appointmentStatus.category.categoryId == 19 && appointmentStatus.lookupId == 43){
			this.appointmentStatus = appointmentStatus;
			//console.log("Function called")
		}
	}
	for(let appointment of this.appointmentList)
	{
		if((appointment.doctor.employeeId == this.addAppointment.doctor.employeeId))
		{
			let appointTime = new Date(appointmentTime);
			let fromDate = new Date(appointment.fromDate);
			if(fromDate.toString() == appointTime.toString())
			{
				for(let appointmentStatus of this.appointmentStatusList)
				{
					if(appointmentStatus.category.categoryId == 19 && appointmentStatus.lookupId == 44)
					{
						this.appointmentStatus = appointmentStatus;
						//console.log("Function called")
					}
				}
				
			}
		}
	}
	if((this.addAppointment.doctor != null)&&(this.addAppointment.patient.mrn != null)){
		this.modalService.open(eventSchedulerForm,{size: 'lg'});
	}
	//console.log(appointmentTime)
	this.addAppointment.fromDate = new Date(appointmentTime);
	this.date = new Date(this.addAppointment.fromDate);
	this.addAppointment.toDate = new Date(this.getToDate(this.date));
	//console.log(this.date)
	//console.log(this.addAppointment)
}
getToDate(appointmentTime){
	let time:number = appointmentTime.getMinutes();
	time = time+this.interval;
	appointmentTime.setMinutes(time);
	return appointmentTime;
}
saveAppointment(){
	this.addAppointment.appointmentStatus = this.appointmentStatus;
	this.addAppointment.patient = this.patient;
	this.appointmentService.createAppointment(this.addAppointment).subscribe(data =>{
		console.log(data);
		alert("sucessfully inserted");
		this.appointmentService.getAppointmentList().subscribe(data =>{
		this.appointmentList = data;
		this.modalService.dismissAll();
		console.warn(data);
	});
	
	})
	
}


	booked(empId){
		var booked = 0;
		for(let appointment of this.appointmentList){
			if(appointment.doctor.employeeId == empId){
				//console.log("doctor matched!!!" + booked);
				if(appointment.appointmentStatus.lookupId == 43){
					booked += 1;
					//console.log("lookup  matched!!! + booked");
				}
			}
		}
		return booked;
	}
	
		confirmed(empId){
		var booked = 0;
		for(let appointment of this.appointmentList){
			if(appointment.doctor.employeeId == empId){
				//console.log("doctor matched!!!" + booked);
				if(appointment.appointmentStatus.lookupId == 48){
					booked += 1;
					//console.log("lookup  matched!!! + booked");
				}
			}
		}
		return booked;
	}
	
		waiting(empId){
		var booked = 0;
		for(let appointment of this.appointmentList){
			if(appointment.doctor.employeeId == empId){
				//console.log("doctor matched!!!" + booked);
				if(appointment.appointmentStatus.lookupId == 44){
					booked += 1;
					//console.log("lookup  matched!!! + booked");
				}
			}
		}
		return booked;
	}
	deleteAppointment(appoint){
		for(let appointmentStatus of this.appointmentStatusList)
				{
					if(appointmentStatus.category.categoryId == 19 && (appointmentStatus.lookupId == 52))
					{
						this.cancelledAppointmentStatus = appointmentStatus;
						//console.log("Function called")
					}
				}
		
		this.toBeDeleteAppointment = appoint;
		this.toBeDeleteAppointment.appointmentStatus = this.cancelledAppointmentStatus;
		this.appointmentService.updateAppointment(this.toBeDeleteAppointment.appointmentId,this.toBeDeleteAppointment).subscribe(data =>{
			alert("cancelled successfully");
			this.appointmentService.getAppointmentList().subscribe(data =>{
		this.appointmentList = data;
		});
		})
		/*
		this.appointmentService.deleteAppointment(appoint.appointmentId).subscribe(data =>{
			console.log(data);
			this.modalService.dismissAll();
			alert("Appointment Deleted!!!!!!!")
			this.appointmentService.getAppointment().subscribe(data =>{
		this.appointmentList = data;
		}); 
		
	});*/
	}
	onCreate(container){
		this.modalService.open(container);
	}
	
	encounter(appoint){
		for(let appointmentStatus of this.appointmentStatusList){
			if(appointmentStatus.lookupId  == 48){
				appoint.appointmentStatus = appointmentStatus;
				this.appointmentService.updateAppointment(appoint.appointmentId, appoint).subscribe(data =>{
					alert("successfully updated!!!!!");
				});
			}
		}
		let data = JSON.stringify(appoint)
		this.router.navigate(['/encounter'],{state : appoint});
	}

}