import { Component, Input, OnInit, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { LookupEntity } from '../../master/lookup-module/masters/lookup-entity';
import { Patient } from '../../patient/master/patient';
import { PatientService } from '../service/patient.service';

@Component({
  selector: 'app-patient-control',
  templateUrl: './patient-control.component.html',
  styleUrls: ['./patient-control.component.css']
})
export class PatientControlComponent implements OnInit {
  	public files: any[];
    url: string | ArrayBuffer;
	@Output() patientDetails: Patient = new Patient();
	@Input() patient: Patient = new Patient();
	patientList: Array<Patient> = [];
  constructor(private modalService: NgbModal,
  	private patientService: PatientService
  	) { }

  ngOnInit(): void {
	this.reloadData();

  }
  reloadData(){
	this.patientService.getPatientList().subscribe(data =>{
		this.patientList = data;
		console.log(this.patientList)
	})
}
  onAdd(patients){
		this.patient = patients;
		
			if(this.patient.visitType == null){
		this.patient.visitType = new LookupEntity();
		this.patient.visitType.lookupValue = "firstVisit";
		this.patient.visitType.lookupId  = 47;
		this.modalService.dismissAll();
		}
	}
 onCreate(container){
		this.modalService.open(container);
	}
}
